function resolveWixEcomDeliveryV1ShippingOptionServiceUrl(
  opts: Omit<ResolveUrlOpts, 'domainToMappings'>
) {
  const domainToMappings = {
    'manage._base_domain_': [
      {
        srcPath: '/shipping-option-service',
        destPath: '',
      },
      {
        srcPath: '/_api/shipping-option-service',
        destPath: '',
      },
    ],
    'www.wixapis.com': [
      {
        srcPath: '/ecom/v1/shipping-options',
        destPath: '/v1/shipping-options',
      },
    ],
    '*.dev.wix-code.com': [
      {
        srcPath: '/ecom/v1/shipping-options',
        destPath: '/v1/shipping-options',
      },
    ],
    _: [
      {
        srcPath: '/ecom/v1/shipping-options',
        destPath: '/v1/shipping-options',
      },
    ],
  };

  return resolveUrl(Object.assign(opts, { domainToMappings }));
}

